import { useEffect, useState } from 'react';
import { Box, Container, Drawer, LinearProgress, ListItem, ListItemIcon, Avatar } from '@mui/material';
import { Typography, Button, AppBar, Menu, MenuItem, List } from '@mui/material';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { useSession } from './SessionContext';
import { readFile } from '../lib/api/public';
import BusinessCenterIcon from '@mui/icons-material/BusinessCenter';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import AssistantIcon from '@mui/icons-material/Assistant';
import PeopleIcon from '@mui/icons-material/People';
import SettingsIcon from '@mui/icons-material/Settings';
import BusinessIcon from '@mui/icons-material/Business';
import CalendarIcon from '@mui/icons-material/CalendarMonth';
import { CreditCard } from '@mui/icons-material';
import { getUrlApp } from '../lib/api/public';

const LoggedInWrapper = ({ children, user, title }) => {
  const router = useRouter();

  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const renderIcon = () => {
    if (router.pathname.includes('job')) {
      return <BusinessCenterIcon />;
    }

    if (router.pathname.includes('candidate')) {
      return <PeopleIcon />;
    }

    if (router.pathname.includes('clients')) {
      return <BusinessIcon />;
    }

    if (router.pathname.includes('settings/users')) {
      return <PeopleIcon />;
    }

    if (title === 'Settings') {
      return <SettingsIcon />;
    }

    if (title === 'Calendar') {
      return <CalendarIcon />;
    }

    if (title === 'Profile') {
      return <AccountCircleIcon />;
    }

    if (title === 'Billing') {
      return <CreditCard />;
    }

    if (title === 'Organization configuration') {
      return <BusinessIcon />;
    }

    if (title === 'AI Editor') {
      return <AssistantIcon />;
    }

    return null;
  }

  return (
    <>
      <AppBar sx={{
        backgroundColor: '#fff',
        height: '50px',
        color: 'black',
        position: 'relative',
        padding: '10px 20px',
        boxShadow: 'none',
        width: '92%',
        marginLeft: '8%' }}
      >
          <div style={{ position: 'absolute', top: '13px' }}>
            {renderIcon()}
          </div>

          <Typography variant="h6" sx={{ marginLeft: '25px'}}>
            {title}
          </Typography>

          <div style={{ position: 'absolute', right: '10px' }}>
            <span>{user && user.organizationName}</span>

            <Button
              aria-controls="menu"
              aria-haspopup="true"
              onClick={handleMenuClick}
              className='go-to-profile'
              sx={ user && user.avatarUrl ? {
                backgroundColor: 'none',
              } : {
                backgroundColor: '#dcd6d8',
              }}
            >
              {user ? user.avatarUrl ? <Avatar src={readFile(user.avatarUrl)} sx={{ height: '30px', width: '30px' }} /> : user.email[0] : ''}
            </Button>

            <div style={{ marginLeft: 8 }}>
            </div>

            <Menu
              id="menu"
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleMenuClose}
            >
              <MenuItem>
                <Link href="/profile">Profile</Link>
              </MenuItem>

              {
                user && user.role === 'superAdmin' && (
                  <MenuItem>
                    <Link href="/settings/billing">Billing</Link>
                  </MenuItem>
                )
              }

              <MenuItem>
                <Link href="/auth/logout">Logout</Link>
              </MenuItem>
            </Menu>
          </div>
      </AppBar>

      <main style={{ padding: '20px' }}>
        {children}
      </main>
    </>
  );
};

type Props = {
  children: any;
  title: React.ReactNode;
  sideBar?: any;
  isLoading?: any;
}

const renderMenuItem = (text: string, link: string, Icon) => {
  return (
    <Link href={link} style={{ textDecoration: 'none', color: 'inherit' }}>
      <ListItem sx={{
        paddingLeft: '0px',
        ":hover": {
          backgroundColor: '#3a3737'
        }
        }}>

        <ListItemIcon
          sx={{
            color: 'white',
            minWidth: 'auto',
            marginRight: '5px',
            marginLeft: '5px',
          }}
        >
          <Icon />

        </ListItemIcon>

        <span>{text}</span>
      </ListItem>
    </Link>
  )
}

